<template>
    <div class="reviewCard">
      <b-container>
       <b-alert :show="submit" :variant="error.deleted ? 'success' : 'danger'">{{error.deleted ? 'Review successfully deleted' : error.msg}}</b-alert>
      </b-container>
      <template v-if="reviews.length!=0">
        <b-card
        v-for="(review,index) in reviews" :key="index"
        border-variant="secondary"
        header-border-variant="secondary"
        :header="review.username"
        class="m-3" :class="(user._id == review.user_id ? 'app-review-card': '')"
      >
        <b-card-text class="text-justify"> {{review.content}}
        </b-card-text>
        <b-button v-if="user._id == review.user_id" variant="info" class="app-button-review-card" @click="updateReview(review._id)"><b-icon icon="pencil-square"> </b-icon>Update your review</b-button>
        <b-button v-if="user._id == review.user_id" variant="danger" class="app-button-review-card app-margin-left" v-b-modal.confirm-delete><b-icon icon="trash"> </b-icon>Delete</b-button>
         <b-modal v-if="user._id == review.user_id" id="confirm-delete" @ok="deleteReview(review._id)">
          <p class="my-4">Are you sure you want to delete this review?</p>
        </b-modal>
      </b-card>
      </template>
      <template v-else>
        <p class="text-center"> There are no reviews availables </p>
      </template>
    </div>
</template>

<script>

const api_url = process.env.VUE_APP_API_URL;

export default {
    name: 'ReviewCard',

    data:function(){
        return{
          reviews: '',
          user: '',
          submit: false,
          error: { deleted: Boolean, msg: String}
        }
    },
    props:{
          movie_id: String | Number,
          token: String
       },
     created: function(){
       this.init();
     },
     methods: {
       init(){
         this.getReviews();
         this.getUser();
       },
       
       getReviews(){
         this.axios.get(`${api_url}/review/for/${this.movie_id}`).then((response)=>{
           this.reviews = response.data.reviews;
         }).catch((error)=>{
           console.log(error.response);
         })
       },
       getUser(){
         if(this.token){
         this.axios.get(`${api_url}/user`,{headers:{
           authorization: this.token
         }
         }).then((response)=>{
           this.user = response.data.user;
         }).catch((error)=>{
           console.log(error);
         })
         }
       },

      updateReview(review_id){
         this.$router.push({path: `/review/update/${review_id}`});
       },

       deleteReview(review_id){
         this.submit = true;
         this.axios.delete(`${api_url}/review/${review_id}`,{headers:{
           authorization: this.token
         }
         }).then((response)=>{
           this.error.deleted = true;
           setTimeout(()=>this.$router.push({path:'/'}),800);
         }).catch((error)=>{
           console.log(error.response);
           this.error.deleted = false;
           this.error.msg = error.response.data.errors.msg;

         })
       }
     }
}
</script>

<style scoped>

.card-header{
  font-style:italic;
}

.app-review-card{
  border-color:#f73859 !important;
}

.card-header::before{
  content:'by ';
  font-size:0.8em;
  font-style:italic;
}

.app-margin-left{
  margin-left:0.5em;
}

.app-button-review-card svg{
  margin-right:0.5em;
}

</style>