<template>

<div class="reviewUpdate">
    <div class="ml-5 mr-5 mt-5">
        <p class="h1"> Your preview review</p>
        <b-row class="mt-5">
            <b-col lg="8" sm="8" class="m-auto">
                <b-card
                border-variant="secondary"
                :header="review.movie_title"
                header-border-variant="secondary"
                >
                    <b-card-text>{{review.content}}</b-card-text>
                </b-card>
            </b-col>
        </b-row>
    </div>
    <div class="ml-5 mr-5 mt-5">
        <p class="h1"> Update your review </p>
        <ReviewForm :type="type"/>
    </div>
</div>

</template>

<script>
import ReviewCard from '../components/shared/Review-card'
import ReviewForm from '../components/shared/Review-form';

const api_url = process.env.VUE_APP_API_URL;

export default {
    name: 'Review-update',
    data(){
        return{
            review_id : this.$route.params.review_id,
            review: '',
            type: 'Update'
        }
    },
    components:{
        ReviewCard, ReviewForm
    },
    created:function(){
        this.init();
    },
    methods:{
        init(){
            this.getReview()
        },
        getReview(){
            this.axios.get(`${api_url}/review/${this.review_id}`).then((response)=>{
                this.review = response.data.review;
            }).catch((error)=>{
                console.log(error.data);
            })
        }
    }
}
</script>

