<template>

<div class="reviewForm">
<b-row>
    <b-col lg="8" sm="8" class="m-auto">
    <b-alert :show="submit" :variant="(error.updated ? 'success' : 'danger')">
    <p class="h4 alert-heading">{{ error.updated ? 'Your review has been created successfully' : 'Your review cannot be created!'}}</p>
    <template v-if="!error.updated">
      <p v-for="(msg, index) in error.msg" :key="index">
      {{ msg }}
      </p>
    </template>
  </b-alert>
    <b-form @submit.prevent="send">
      <b-form-group
        id="input-group-username"
        label="Username"
        label-for="input-username"
        :description="review.username=='' ? 'Required' : ''"
      >
        <b-form-input
          id="input-username"
          type="text"
          class="vf-only-bottom-border vf-input-focus" v-model="review.username"></b-form-input>
          <template v-if="review.username">
          <b-tooltip target="input-username" triggers="focus">
          If you change this, your username will be <b>updated</b>!
          </b-tooltip>
      </template>
      <b-form-checkbox
      v-model="checkUsername"
      class="mb-3 mt-4"
      value-field="acepted"
      text-field="name"
      :state="checkUsername"
      disabled-field="notEnabled"
       v-if="user.username != review.username">I'm sure I want to update my username</b-form-checkbox>
      <b-form-invalid-feedback :state="checkUsername" v-if="submit && !checkUserUsernameEqualsReviewUsername()">You have to accept!</b-form-invalid-feedback>
  </b-form-group>


  <b-form-textarea
    id="textarea-no-resize"
    placeholder="Tell us your opinion"
    rows="3"
    no-resize
    v-model="review.content"
    required
  ></b-form-textarea>
  <b-form-group class="mt-3">
      <b-button type="submit" class="mr-2">Send</b-button>
  </b-form-group>
    </b-form>
    </b-col>
    </b-row>

    </div>

</template>

<script>


const api_url = process.env.VUE_APP_API_URL;

export default {
    name:'Review-form',
        data(){
        return{
            checkUsername: false,
            submit: false,
            error: {
              updated: false,
              msg:'',
            },
            user:'',
            review:{
              username : '',
              content: ''
            },
            review_id: this.$route.params.review_id,
            movie_id: this.$route.params.movie_id
        }
    },
    props:{
        type: String
    },
    created: function(){
        this.init();
    },
    methods:{
        init(){
          this.getUser();
        },
        async getUser(){
          try{
        let response = await this.axios.get(`${api_url}/user/profile`, {
          headers:{
            authorization: this.token
          }
        });
          this.user = response.data.user
          this.getUsername();
          }catch(error){
            console.log(error);
          }
        },

        send(){
        this.submit = true;
        if(this.user.username == this.review.username){
            switch(this.type){
                case 'Send':
                    this.sendReview();
                break;

                case 'Update':
                    this.updateReview();
                break;
            }

        }else{
            if(this.checkUsername){
              switch(this.type){
                case 'Send':
                    this.sendReview();
                break;

                case 'Update':
                    this.updateReview();
                break;
            }
          }else{
            let error = { msg : 'You must accept!' };
            this.error.msg = error;
          }
        }

        },

      
      sendReview(){
            this.axios.post(`${api_url}/review/movie/${this.movie_id}`,this.review,{
              headers:{
                authorization: this.token
              },
          }).then((response)=>{
            if(response.data.token){
            this.$store.dispatch('refreshToken',response.data.token).then(()=>{
            }).catch((error)=>{
              console.log(error); 
            });

            }
            this.error.updated = true;
            setTimeout(()=>this.$router.go('-1'),800);
          }).catch((error)=>{
            this.error.updated = false;
            this.error.msg = error.response.data.errors.msg;
            console.log(this.error.msg);
            })
        },

        updateReview(){
            this.axios.put(`${api_url}/review/${this.review_id}`,this.review,{
              headers:{
                authorization: this.token
              },
          }).then((response)=>{
            if(response.data.token){
              this.$store.dispatch('refreshToken',response.data.token).then(()=>{
              }).catch((error)=>{
                  console.log(error);
            });  
          }
            this.error.updated = true;
            setTimeout(()=>this.$router.go('-1'),800);
          }).catch((error)=>{
            this.error.updated = false;
            if(this.error.msg){
              this.error.msg = error.response.data.errors.msg;
            }
           
            })
        },
        
        // utility methods
        
        checkUserUsernameEqualsReviewUsername(){
          return this.user.username == this.review.username
        },
        
        getUsername(){
          this.review.username = this.user.username
        },

        },
        computed:{
          token : function() { return this.$store.getters.isAuth }
        }
}
</script>

<style scoped>


button[type="submit"]{
  width:10em;
  background-color:#f73859;
}

button[type="submit"]:active{
  background-color:#ff173e !important;
  box-shadow:none !important;
  outline:none !important;
}

textarea:focus{
  outline:#f73859 !important;
  border: 0.1em solid #f73859;
  box-shadow: 1px 1px 5px #f73859;
}

input{
  color:rgba(0, 0, 0, 0.650);
}

</style>